import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-product-attribute',
	styleUrl: 'product-attribute.css'
})
export class ProductAttribute {
    @Prop() href: string = "";
    
    @Prop() icon: string = "";
    @Prop() size: number = 1.0;

    @Prop({reflect: true}) danger: boolean = false;
    @Prop({reflect: true}) emphasis: boolean = false;
    @Prop({reflect: true}) faded: boolean = false;

	render() {
        const content = [
            <ks-icon name={this.icon} size={this.size}/>,
            <slot/>
        ];

        if(this.href != "")
            return <a href={this.href} target="_blank">{content}</a>;
		return content;
	}
}